/**
 * Универсальный метод склонения слов в зависимости от числа перед ним
 *
 * @param {number} num число под которое форматируется слово
 * @param {Array<string>} nouns массив слов, например: варианты написания для количества 1, 2 и 5
 *  ['комментарий','комментария','комментариев']
 * @return {string}
 */

export default function getNoun(num: number, nouns: Array<string>): string {
  let n = Math.abs(num);

  n %= 100;
  if (n >= 5 && n <= 20) {
    return nouns[2];
  }

  n %= 10;
  if (n === 1) {
    return nouns[0];
  }

  if (n <= 4 && n > 0) {
    return nouns[1];
  }

  return nouns[2];
}
